<template>
	<!--  -->
	<div style="height: inherit">
		<el-row type="flex" justify="space-between" style="margin:12px 12px">
			<el-col :xl="{ span: 16 }" :span="15"><el-input id="searchControl" v-model="search" placeholder="" prefix-icon="el-icon-search"></el-input></el-col>
			<el-col :xl="{ span: 6 }" :span="9" style="width: auto;">
				<tip-button content="" :open-delay="500" type="success" icon="el-icon-plus" circle @click.native="createTask(null)"></tip-button>
			</el-col>
		</el-row>
		<el-row>
			<el-tabs type="border-card" v-model="selectTab" tab-position="top">
				<el-tab-pane label=""></el-tab-pane>
				<el-tab-pane label=""></el-tab-pane>
				<el-tab-pane label=""></el-tab-pane>
				<el-tab-pane label=""></el-tab-pane>
			</el-tabs>
		</el-row>
		<el-row style="height: 90%;">
			<el-scrollbar style="height: inherit;">
				<el-card shadow="hover" v-if="filterTask.length == 0"><p style="text-align: center;"></p></el-card>
				<transition-group name="el-zoom-in-top" mode="out-in" :appear="true" type="transition" v-else>
					<template v-for="(item, index) in filterTask.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))">
						<el-card shadow="hover" :class="item.status == 1 ? 'success' : item.status == 2 ? 'warning' : 'info'" :key="index">
							<span style="white-space:normal;width: 200px;display: inline-flex;cursor: pointer;" @click="openResult(item)">{{ item.name }}</span>
							<el-date-picker readonly :type="item.type == 1 ? 'datetime' : 'datetimerange'" :value="item.type == 1 ? new Date(item.startTime) : [new Date(item.startTime), new Date(item.endTime)]" value-format="yyyy:mm:dd hh:mm:ss" class="timePicker"></el-date-picker>
							<span style="float: right;">
								<my-popconfirm v-if="item.status == 1" @on-confirm="stopTask(index, item)" content="" size="small" placement="top" icon="el-icon-switch-button"> 
								<font color="#F56C6C"></font>
								</my-popconfirm>
								<my-popconfirm v-else-if="item.status == 2" @on-confirm="restartTask(index, item)" content="" type="primary" size="small" placement="top" icon="el-icon-refresh-right">
									<font color="#F56C6C"></font>
								</my-popconfirm>
								<my-popconfirm v-else @on-confirm="startTask(index, item)" content="" type="success" size="small" placement="top" icon="el-icon-switch-button">
									<font color="#F56C6C"></font>
								</my-popconfirm>
								<tip-button content="" :open-delay="500" type="warning" size="small" icon="el-icon-edit-outline" circle @click.native="createTask(item)" style="margin-left: 10px;"></tip-button>
								<my-popconfirm @on-confirm="deleteTask(index, item)" content="" size="small" placement="top">
									<font color="#F56C6C"></font>
								</my-popconfirm>
							</span>
							<div><el-tag :type="item.type == 1 ? '':item.type == 2 ? 'success':'danger'" size="mini">{{item.type == 1 ? "":item.type == 2 ? "":""}}</el-tag></div>
						</el-card>
					</template>
				</transition-group>
			</el-scrollbar>
		</el-row>
		<el-dialog :visible.sync="formPage" width="60%" custom-class="form-class" top="7vh" center="center">
			<div slot="title"></div>
			<el-scrollbar style="height: 70vh; width: 100%;">
				<div style="padding-right: 15px;">
					<el-form ref="taskForm" :model="taskForm" label-width="80px">
						<el-form-item label=""><el-input v-model="taskForm.name"></el-input></el-form-item>
						<el-form-item label="">
							<el-date-picker v-show="taskForm.type == 1" v-model="taskForm.time" type="datetime" placeholder="" align="right" :picker-options="{disabledDate(time) {return time.getTime() <= Date.now();}}"></el-date-picker>
							<el-date-picker v-show="taskForm.type == 2" v-model="taskForm.time" type="datetimerange" range-separator="-" start-placeholder="" end-placeholder="" :picker-options="taskForm.pickerOptions" :unlink-panels="true" :default-time="Date.now()"></el-date-picker>
						</el-form-item>
						<el-form-item label="">
							<el-select v-model="taskForm.type" placeholder="" popper-class="type-class">
								<el-option v-for="item in taskForm.typeOption" :key="item.value" :label="item.label" :value="item.value"></el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="">
							<el-input v-model="taskForm.Msg" placeholder=""></el-input>
						</el-form-item>
						<el-form-item :label="taskForm.type == 1 ? '':''">
							<el-switch v-model="taskForm.isPrivate" active-text="" inactive-text=""></el-switch>
						</el-form-item>
						<span v-show="taskForm.type == 1">
							<el-form-item label="">
								<el-switch v-model="taskForm.isRepeat" active-text="" inactive-text=""></el-switch>
							</el-form-item>
							<el-form-item label="">
								<el-input v-model="taskForm.repeatTime" placeholder=",."></el-input>
							</el-form-item>
							<el-form-item label="">
								<el-date-picker v-model="taskForm.endTime" type="datetime" placeholder="" align="right" :picker-options="{disabledDate(time) {return time.getTime() <= Date.now();}}"></el-date-picker>
							</el-form-item>
						</span>
						<span v-show="taskForm.type == 2">
							<el-form-item label="">
								<el-col :span="18"><el-input v-model="taskForm.Regex" placeholder=","></el-input></el-col>
								<el-col :span="5" :offset="1">
									<el-popover placement="top" width="400" trigger="click" popper-class="textPattern-class" effect="dark">
										<el-form-item>
											<el-input v-model="myTestPattern.text" placeholder=""><span slot="prepend"></span></el-input>
										</el-form-item>
										<el-form-item>
											<el-input v-model="myTestPattern.pattern" placeholder=",">
												<span slot="prepend"></span>
											</el-input>
										</el-form-item>
										<el-form-item>
											<el-input disabled v-model="myTestPattern.result"><span slot="prepend"></span></el-input>
										</el-form-item>
										<el-form-item>
											<span style="padding-left: 10px;">
												<el-tooltip placement="left">
													<el-scrollbar slot="content" style="height: 30vh; ">
														<div class="tipPattern">
															<el-link v-for="(item, index) in tipPattern" style="font-size: 14px; color: #fff;line-height: 1.7;display: block;" @click="myTestPattern.pattern = item.pattern" :key="index" :underline="false">
																{{ item.title }}{{ item.pattern }}
															</el-link>
														</div>
													</el-scrollbar>
													<el-link type="primary" style="text-align:start"></el-link>
												</el-tooltip>
											</span>
											<span style="float: right;">
												<el-button type="success" @click="changeTestPattern"></el-button>
												<el-button type="primary" @click="insertPattern"></el-button>
											</span>
										</el-form-item>
										<el-button type="primary" slot="reference"></el-button>
									</el-popover>
								</el-col>
							</el-form-item>
							<el-form-item label="">
								<el-input v-model="taskForm.Reply" placeholder="."></el-input>
							</el-form-item>
							<el-form-item label="">
								<el-input v-model="taskForm.endMsg" :placeholder="',' + (taskForm.name || '\'\'') + ''"></el-input>
							</el-form-item>
							<el-form-item label="">
								<el-input v-model="taskForm.remindTime" placeholder=","></el-input>
								<div style="margin-top: 10px;"></div>
								<el-input v-model="taskForm.remindMsg" placeholder=""></el-input>
								<div style="margin-top: 10px;"></div>
								<span></span><el-switch v-model="taskForm.remindIsPrivate" active-text="" inactive-text=""></el-switch>
							</el-form-item>
							<el-form-item label="">
								<el-transfer v-model="taskForm.saveMsg" :data="saveDateList" :titles="['', '']"></el-transfer>
							</el-form-item>
						</span>
						<el-form-item label="" v-show="(taskForm.type == 1 && taskForm.isPrivate) || taskForm.type == 2">
							<el-col :span="14">
								<el-upload
									action="https://jsonplaceholder.typicode.com/posts/"
									:drag="true"
									accept=".xls"
									:on-success="listSuccess"
									:on-error="listError"
									auto-upload
								>
									<i class="el-icon-upload"></i>
									<div class="el-upload__text">
										
										<em></em>
									</div>
								</el-upload>
							</el-col>
							<el-col :span="10">
								<div>xls</div>
								<el-image :src="require('../assets/example.jpg')" style="height: 139px;"></el-image>
							</el-col>
						</el-form-item>
						
						<div class="el-form-item" style="text-align: center;">
							<div class="el-form-item__content">
								<el-button></el-button>
								<el-button type="primary" @click="onSubmit">{{taskForm.dialogButtonName}}</el-button>
							</div>
						</div>
					</el-form>
				</div>
			</el-scrollbar>
		</el-dialog>

		<el-dialog :visible.sync="showTaskResult" width="60%" custom-class="form-class" top="7vh" center="center">
			<span slot="title">{{taskResult.item.name}}-</span>
			<el-table :data="taskResult.data" border style="width: 100%" max-height="70vh" size="small">
				<el-table-column prop="qq" label="QQ" width="120"></el-table-column>
				<el-table-column prop="name" label="" width="180"></el-table-column>
				<el-table-column label="" width="100">
					<template slot-scope="scope">
						<span :style="{'color':scope.row.finish ? '#67C23A':'#F56C6C'}">{{ scope.row.finish ? '':'' }}</span>
					</template>
				</el-table-column>
				<el-table-column v-for="(item,index) in taskResult.format" :key="index" :prop="'data' + index" :label="''+index"></el-table-column>
			</el-table>
			<div class="el-form-item" style="text-align: right;margin-top: 10px;">
				<div class="el-form-item__content">
					<el-button size="small" @click="showTaskResult=false"></el-button>
					<el-button size="small" type="primary" @click="downLoad"></el-button>
				</div>
			</div>
		</el-dialog>
	</div>
</template>

<script>
import { decode, encode } from './base64Code.js';
import TipButton from '../packages/button/src/tipButton.vue';
import MyPopconfirm from './MyPopconfirm.vue';
function calcPath(idList, pathList, type) {
	var routerMap = [];
	if ((idList == null || pathList == null || pathList.length != idList.length) && (type == null || type == 'folder')) return routerMap;
	var id = '';
	var path = '';
	for (var i = 1; i < pathList.length; i++) {
		path = path + '/' + pathList[i];
		id = id + '/' + idList[i];
		var to = '/?path=' + encode(path) + '&id=' + encode(id);
		if (i == pathList.length - 1) {
			to = '';
			pathList[i] = pathList[i] + '';
		}
		routerMap.push({ name: pathList[i], to: to });
	}
	return routerMap;
}

export default {
	data() {
		return {
			selectTab: 0,
			search: '',
			formPage: false,
			showTaskResult: false,
			taskResult: {
				data:[
					{
						qq:'123456789',
						name:'',
						finish:true,
						data0:"",
						data1:"96894",
						data2:"2018-20-5",
					}
				],
				format:[],
				item:{},
			},
			taskForm: {
				M_ID:'',				//id
				name: '',				//
				isPrivate:false,		//
				startTime: '',			//
				endTime: '',			//
				endMsg:'',				//
				Msg:'',					//
				Regex:'',				//
				Reply:'',				//
				remindMsg:'',			//
				remindIsPrivate:false,	//
				remindTime:'',			//
				saveMsg:[],				//
				groupID:'',				//
				groupList:'',			//
				isRepeat:false,			//
				repeatTime:"",			//
				type:'1',				//
				time: [],
				dialogButtonName:'',
				pickerOptions: {
					disabledDate(time) {
						return time.getTime() <= Date.now();
					},
					shortcuts: [
						{
							text: '',
							onClick(picker) {
								const start = new Date();
								const end = new Date();
								end.setTime(end.getTime() + 3600 * 1000 * 24 * 7);
								picker.$emit('pick', [start, end]);
							}
						},
						{
							text: '',
							onClick(picker) {
								const start = new Date();
								const end = new Date();
								end.setTime(end.getTime() + 3600 * 1000 * 24 * 30);
								picker.$emit('pick', [start, end]);
							}
						},
						{
							text: '',
							onClick(picker) {
								const start = new Date();
								const end = new Date();
								end.setTime(end.getTime() - 3600 * 1000 * 24 * 90);
								picker.$emit('pick', [start, end]);
							}
						}
					]
				},
				typeOption: [
					{
						value: '1',
						label: ''
					},
					{
						value: '2',
						label: ''
					}
				],
			},
			myTestPattern: {
				text: '',
				pattern: '',
				result: ''
			},
			allTask: [
				{
					M_ID: '1',							//id						all
					name: '',					//						all
					isPrivate:false,					//			1,;2,
					startTime: '2020-5-17 00:00:00',	//						all
					endTime: '2020-5-31 00:00:00',		//		all
					endMsg:'',							//						1
					Msg:'',								//						all
					Regex:'',							//						1
					Reply:'',							//						1
					remindMsg:'',						//						1
					remindIsPrivate:false,				//					1
					remindTime:'',						//						1
					saveMsg:[1,2],						//						1
					groupID:"986675982",				//							all
					status:"1",							//						all
					groupList:"",						//							all
					isRepeat:false,						//				2
					repeatTime:"300000",				//						2
					type:'1',							//						
				}
			],
			filterTaskMap: new Map(),
			tipPattern: [
				{ title: ' ', pattern: '^[0-9]*$' },
				{ title: ' n', pattern: '^\d{n}$' },
				{ title: ' n', pattern: '^\d{n,}$' },
				{ title: ' m-n', pattern: '^\d{m,n}$' },
				{ title: ' ', pattern: '^(0|[1-9][0-9]*)$' },
				{ title: ' ', pattern: '^([1-9][0-9]*)+(.[0-9]{1,2})?$' },
				{ title: ' 1-2', pattern: '^(\-)?\d+(\.\d{1,2})?$' },
				{ title: ' ', pattern: '^(\-|\+)?\d+(\.\d+)?$' },
				{ title: ' ', pattern: '^[0-9]+(.[0-9]{2})?$' },
				{ title: ' 1~3', pattern: '^[0-9]+(.[0-9]{1,3})?$' },
				{ title: ' ', pattern: '^[1-9]\d*$' },
				{ title: ' ', pattern: '^-[1-9]\d*$' },
				{ title: ' ', pattern: '^\d+$' },
				{ title: ' ', pattern: '^-[1-9]\d*|0$' },
				{ title: ' ', pattern: '^\d+(\.\d+)?$' },
				{ title: ' ', pattern: '^((-\d+(\.\d+)?)|(0+(\.0+)?))$' },
				{ title: ' ', pattern: '^[1-9]\d*\.\d*|0\.\d*[1-9]\d*$' },
				{ title: ' ', pattern: '^-([1-9]\d*\.\d*|0\.\d*[1-9]\d*)$' },
				{ title: ' ', pattern: '^(-?\d+)(\.\d+)?$' },

				{ title: ' ', pattern: '^[\u4e00-\u9fa5]{0,}$' },
				{ title: ' ', pattern: '^[A-Za-z0-9]+$' },
				{ title: ' 3-20', pattern: '^.{3,20}$' },
				{ title: ' 26', pattern: '^[A-Za-z]+$' },
				{ title: ' 26', pattern: '^[A-Z]+$' },
				{ title: ' 26', pattern: '^[a-z]+$' },
				{ title: ' 26', pattern: '^[A-Za-z0-9]+$' },
				{ title: ' 26', pattern: '^\w+$' },
				{ title: ' ', pattern: '^[\u4E00-\u9FA5A-Za-z0-9_]+$' },
				{ title: ' ', pattern: '^[\u4E00-\u9FA5A-Za-z0-9]+$' },

				{ title: 'Email', pattern: "[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?" },
				{ title: 'url', pattern: '[a-zA-z]+://[^\s]*' },
				{ title: '', pattern: '\d{3}-\d{8}|\d{4}-\{7,8}' },
				{ title: 'QQ', pattern: '[1-9][0-9]{4,}' },
				{ title: '', pattern: '[1-9]\d{5}(?!\d)' },
				{ title: '18', pattern: '^(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})([0-9]|X)$' }
			]
		};
	},
	methods: {
		deleteTask(index, row) {
			console.log(index, row);
		},
		listSuccess(response, file, fileList) {
			console.log(response, file, fileList);
		},
		listError(err, file, fileList) {
			console.log(err, file, fileList);
		},
		onSubmit() {},
		changeTestPattern() {
			try {
				var reg = new RegExp(this.myTestPattern.pattern, 'gm');
				this.myTestPattern.result = reg.exec(this.myTestPattern.text)[0];
			} catch (e) {
				this.$message({
					dangerouslyUseHTMLString: true,
					message: '' + '<br/><br/>' + e.message,
					type: 'warning',
					center: true
				});
			}
		},
		insertPattern() {
			this.taskForm.pattern = this.myTestPattern.pattern;
		},
		createTask(data) {
			this.taskForm.M_ID = '';
			this.taskForm.name = '';
			this.taskForm.isPrivate = false;
			this.taskForm.time = '';
			this.taskForm.endMsg = '';
			this.taskForm.Msg = '';
			this.taskForm.Regex = '';
			this.taskForm.Reply = '';
			this.taskForm.remindMsg = '';
			this.taskForm.remindIsPrivate = false;
			this.taskForm.remindTime = '';
			this.taskForm.saveMsg = [];
			this.taskForm.groupList = '';
			this.taskForm.isRepeat = false;
			this.taskForm.repeatTime = '';
			this.taskForm.type = '1';
			
			this.myTestPattern.text = '';
			this.myTestPattern.pattern = '';
			this.myTestPattern.result = '';
			this.taskForm.dialogButtonName = "";
			if (data != null) {
				this.taskForm.M_ID = data.M_ID;
				this.taskForm.name = data.name;
				this.taskForm.isPrivate = data.isPrivate;
				this.taskForm.time = data.type == 1 ? data.startTime : [data.startTime, data.endTime];
				this.taskForm.endMsg = data.endMsg;
				this.taskForm.Msg = data.Msg
				this.taskForm.Regex = data.Regex
				this.taskForm.Reply = data.Reply
				this.taskForm.remindMsg = data.remindMsg
				this.taskForm.remindIsPrivate = data.remindIsPrivate
				this.taskForm.remindTime = data.remindTime
				this.taskForm.saveMsg = data.saveMsg
				this.taskForm.groupList = data.groupList
				this.taskForm.isRepeat = data.isRepeat
				this.taskForm.repeatTime = data.repeatTime
				this.taskForm.type = data.type
				this.taskForm.dialogButtonName = "";
			}
			this.formPage = true;
		},
		stopTask(index, item) {
			console.log(index, item);
		},
		restartTask(index, item) {
			console.log(index, item);
		},
		startTask(index, item) {
			console.log(index, item);
		},
		openResult(item) {
			if(item.status == 3 || item.type == 1)
			{
				this.$message({
					message: item.type == 1 ? "":"",
					type: 'warning',
					center: true
				});
			}else
			{
				this.taskResult.item = item;
				this.taskResult.format = item.saveDate;
				this.showTaskResult = true;
			}
		},
		downLoad(){
			console.log(this.taskResult.item)
		}
	},
	mounted() {
		this.$router.app.$data.routerMap = calcPath(this.$props.id.split('/'), this.$props.path.split('/'), this.$props.type);
	},
	props: {
		id: {
			type: String
		},
		path: {
			type: String
		},
		type: {
			type: String
		}
	},
	components: {
		TipButton,
		MyPopconfirm
	},
	computed: {
		filterTask() {
			if (this.selectTab == 0) {
				return this.allTask;
			} else {
				if (this.filterTaskMap.has(this.selectTab)) return this.filterTaskMap.get(this.selectTab);
				var task = [];
				this.allTask.forEach((val, index) => {
					if (val.status == this.selectTab) task.push(val);
				});
				this.filterTaskMap.set(this.selectTab, task);
				return task;
			}
		},
		saveDateList() {
			var data = [];
			var matcher = /\(.[^()]*\)/;
			for (var i = 0, j = 0; i < this.taskForm.Regex.length; j++) {
				var tmp = matcher.exec(this.taskForm.Regex.substr(i));
				if (tmp != null) {
					data.push({ key: j, label: tmp[0], disabled: false });
				}
				i += tmp != null ? tmp.index + tmp[0].length : this.taskForm.Regex.length;
			}
			return data;
		}
	}
};
</script>

<style scoped="scoped">
.el-tabs--border-card {
	border: none;
}
.el-card.success {
	border-left: 5px solid #67c23a;
}
.el-card.warning {
	border-left: 5px solid #e6a23c;
}
.el-card.danger {
	border-left: 5px solid #f56c6c;
}
.el-card.info {
	border-left: 5px solid #909399;
}
.el-card {
	margin-top: 5px;
}
.timePicker {
	/* float: right; */
	margin-left: calc(50% - 430px);
}
.tipPattern h3 {
	font-size: 18px;
	line-height: 1.8;
}
.tipPattern p {
	font-size: 14px;
	line-height: 1.7;
}
</style>



